<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>推送管理</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/pushAllocation' }">推送配置管理</el-breadcrumb-item>
        <el-breadcrumb-item>配置字段</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <div class="MT_table_cont">
        <!-- 按钮 -->
        <div class="btns">
          <el-button @click.stop="add" icon="el-icon-plus">新增</el-button>
          <div class="btn_cont">
            <div class="choose_btn _color">
              <span @click.stop="remove">批量删除</span>
              <span @click.stop="push">生成推送关系</span>
            </div>
          </div>
        </div>

        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
          <el-table-column type="selection" width="55"> </el-table-column>
          <el-table-column prop="sourceFireldName" label="源字段名"> </el-table-column>
          <el-table-column prop="targetFireldName" label="目标字段名"> </el-table-column>
          <el-table-column prop="targetFireldType" label="目标字段类型" width="180"> </el-table-column>
          <el-table-column label="状态" width="120">
            <template slot-scope="scope">
              {{ scope.row.fieldStatus === 1 ? '启用' : '停用' }}
            </template>
          </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="100">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row)">修改</el-link>
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)">删除</el-link>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>

      <!-- 添加/修改 -->

      <el-dialog :title="pageType === 0 ? '新增字段' : '修改字段'" :visible.sync="dialogVisible" width="30%">
        <el-divider></el-divider>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
          <el-form-item label="源字段名" prop="sourceFireldName">
            <el-select v-model="ruleForm.sourceFireldName" placeholder="请选择源字段名">
              <el-option label="源字段名一" value="shanghai"></el-option>
              <el-option label="源字段名二" value="beijing"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="目标字段名" prop="targetFireldName">
            <el-select v-model="ruleForm.targetFireldName" placeholder="请选择目标字段名">
              <el-option label="目标字段名一" value="shanghai"></el-option>
              <el-option label="目标字段名二" value="beijing"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="启用标记" prop="fieldStatus">
            <el-radio-group v-model="ruleForm.fieldStatus">
              <el-radio :label="1">启用</el-radio>
              <el-radio :label="0">禁用</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
            <el-button @click="resetForm('ruleForm')">取消</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
var res = {
  data: [
    { id: 11, sourceFireldName: '这是标题1这是标题1', targetFireldType: '1sdfa2', fieldStatus: 1, targetFireldName: '这是表名1' },
    { id: 23, sourceFireldName: '这是标题2这是标题2', targetFireldType: '1sczxv22', fieldStatus: 0, targetFireldName: '这是表名2' },
    { id: 34, sourceFireldName: '这是标题3这是标题3', targetFireldType: '12sadfgasd2', fieldStatus: 1, targetFireldName: '这是表名3' },
    { id: 25, sourceFireldName: '这是标题4这是标题4', targetFireldType: '1fzsdgf2', fieldStatus: 1, targetFireldName: '这是表名4' },
    { id: 54, sourceFireldName: '这是标题5这是标题5', targetFireldType: '11awetwer2', fieldStatus: 0, targetFireldName: '这是表名5' },
    { id: 23, sourceFireldName: '这是标题6这是标题6', targetFireldType: '1wfasd2', fieldStatus: 1, targetFireldName: '这是表名6' }
  ],
  total: 131
}
var fieldList = [
  { columnname: 'ID' },
  { columnname: 'SCLASS' },
  { columnname: 'SMARK' },
  { columnname: 'SCODE' },
  { columnname: 'SNO' },
  { columnname: 'STATUS' },
  { columnname: 'ISPUBLIC' },
  { columnname: 'EXECUTEDATE' },
  { columnname: 'INITCLASS' },
  { columnname: 'EXPIRYDATE' },
  { columnname: 'SYEAR' },
  { columnname: 'CSNAME' },
  { columnname: 'PAGE' },
  { columnname: 'PRICE' },
  { columnname: 'ICS' },
  { columnname: 'REPLACESD' },
  { columnname: 'REPLACEDSD' },
  { columnname: 'USESD' },
  { columnname: 'ALERTTIME' },
  { columnname: 'CSDC' },
  { columnname: 'CMDC' },
  { columnname: 'CKEYWORD' },
  { columnname: 'OKEYWORD' },
  { columnname: 'FREEWORD' },
  { columnname: 'SKEYWORD' },
  { columnname: 'TEXTINDEX' },
  { columnname: 'EINDEX' },
  { columnname: 'INFORCODE' },
  { columnname: 'SLEVEL' },
  { columnname: 'SECONDNO' },
  { columnname: 'TRANSINDEX' },
  { columnname: 'USERID' },
  { columnname: 'USERNAME' },
  { columnname: 'UPDATETIME' },
  { columnname: 'USERINDEX' },
  { columnname: 'USERCODE' },
  { columnname: 'DESCRIPTION' },
  { columnname: 'ACCORDING' },
  { columnname: 'SDINDEX' },
  { columnname: 'SALESTATUS' },
  { columnname: 'FILEURL' },
  { columnname: 'STYPE' },
  { columnname: 'ISSUEDATE' },
  { columnname: 'PPBJ' },
  { columnname: 'EGNAME' },
  { columnname: 'SIGN' },
  { columnname: 'PUSHMARK' },
  { columnname: 'SCODE_EGNAME_CSNAME' },
  { columnname: 'QUOTESD' },
  { columnname: 'SDRANGE' },
  { columnname: 'CLICKNUM' },
  { columnname: 'SCODE2' },
  { columnname: 'MARKDATE' }
]
export default {
  data() {
    return {
      tableData: [],
      multipleSelection: [],
      queryForm: {
        size: 10,
        page: 1,
        total: ''
      },
      pageType: 0,
      dialogVisible: false,

      // --------------
      ruleForm: {
        sourceFireldName: '',
        targetFireldName: '',
        fieldStatus: 1
      },
      rules: {
        sourceFireldName: [{ required: true, message: '请选择源字段名', trigger: 'blur' }],
        targetFireldName: [{ required: true, message: '请选择目标字段名', trigger: 'blur' }],
        platStatus: [{ required: true, message: '请选择是否启用', trigger: 'change' }]
      }
    }
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          alert('submit!')
          console.log(this.ruleForm)
        } else {
          console.log('error submit!!')
          return false
        }
        this.dialogVisible = false
      })
    },

    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.dialogVisible = false
    },

    // --------------------------------------------

    // 新增
    add() {
      this.pageType = 0
      this.dialogVisible = true
    },

    edit(info) {
      this.ruleForm = info
      this.pageType = 1
      this.dialogVisible = true
    },

    del(id) {
      this.$confirm('此操作将删除数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          this.$message({ type: 'success', message: '删除成功!' })
        })
        .catch(() => {})
    },
    // 批量删除
    remove() {
      if (!this.multipleSelection.length) return this.$message.warning('请选择要删除的数据！')
      this.$confirm('此操作将删除已选中数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          this.$message({ type: 'success', message: '删除成功!' })
        })
        .catch(() => {})
    },

    // 批量推送
    push() {
      if (!this.multipleSelection.length) return this.$message.warning('请选择要推送的数据！')
      this.dialogFormVisible = true
    },

    handleSelectionChange(val) {
      this.multipleSelection = val
    },

    loadData() {
      this.queryForm.total = ''
      console.log(this.queryForm)
      this.tableData = res.data
      this.queryForm.total = res.total
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped></style>
