<template>
  <div>
    <!-- 功能按钮 -->
    <div class="MT_fn_btn">
      <el-form :inline="true" :model="queryForm" class="btn_right demo-form-inline form_border">
        <!-- <div class="tips">默认显示30天内数据</div> -->
        <el-form-item label="" prop="pushPlat">
          <el-form-item label="申请公司" prop="contactName">
            <el-input v-model="queryForm.company" placeholder="请输入申请公司"></el-input>
          </el-form-item>
          <el-form-item label="申请时间" prop="chooseDate">
            <el-date-picker size="small" @change="changeDate" v-model="queryForm.chooseDate" type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click.stop="onSubmit">搜索</el-button>
        </el-form-item>
      </el-form>
    </div>

    <div class="MT_table_cont">
      <!-- 表格列表 -->
      <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%">
        <el-table-column prop="company" label="公司名称"> </el-table-column>
        <el-table-column prop="contactName" label="负责人" width="200"> </el-table-column>
        <el-table-column prop="contactPhone" label="联系方式" width="150"> </el-table-column>
        <el-table-column prop="createDate" label="申请时间" width="200"> </el-table-column>
        <el-table-column prop="status" label="状态" width="150">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.status == 0">申请中</el-tag>
            <el-tag type="warning" v-else-if="scope.row.status == 1">查新中</el-tag>
            <el-tag type="success" v-else>查新完成</el-tag>
          </template>
        </el-table-column>
        <!-- 操作 -->
        <el-table-column label="操作" width="120">
          <template slot-scope="scope">
            <el-link :underline="false" v-if="scope.row.status != 2" type="primary" @click.stop="check(scope.row, 'search')">检索</el-link>
            <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)">删除</el-link>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页器 -->
      <div class="MT_pagination" v-if="queryForm.total">
        <span class="jump_first" @click.stop="$jumpToFirst(queryForm, changePage)">首页</span>
        <span class="jump_last" @click.stop="$jumpToLast(queryForm, changePage)">尾页</span>
        <el-pagination
          background
          :total="queryForm.total"
          :current-page.sync="queryForm.page"
          :page-size.sync="queryForm.size"
          @current-change="changePage"
          layout="total, prev, pager, next, jumper"
        />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['dataInfo', 'queryForm'],
  data() {
    return {
      // queryForm:{
      //   platCode:''
      // },
      tableData: [],
      multipleSelection: [],
      platformList: []
    }
  },

  methods: {
    // 查看
    check(info) {
      this.$router.push({ path: '/newQueryDetail', name: 'NewQueryDetail', params: { info: info } })
      this.$sessionData('set', 'checkInfo', info)
    },
    changeDate(e) {
      if (!e) {
        this.queryForm.startDate = ''
        this.queryForm.endDate = ''
      } else {
        this.queryForm.startDate = this.$moment(e[0]).format('YYYYMMDD')
        this.queryForm.endDate = this.$moment(e[1]).format('YYYYMMDD')
      }
      console.log(this.queryForm)
    },
    del(id) {
      this.$confirm('此操作将删除数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          this.httpPost('/standard/new/rem/' + id, '').then(ret => {
            this.$message({ type: 'success', message: '删除成功!' })
            this.$emit('reLoad', 1)
          })
        })
        .catch(() => {})
    },

    // 页码改变
    changePage() {
      this.$emit('getPage', this.queryForm.page)
    },
    // 搜索
    onSubmit() {
      this.$emit('getQueryForm', this.queryForm)
    }
  },

  watch: {
    dataInfo(newVal, oldVal) {
      this.tableData = newVal
    }
  },
  mounted() {}
}
</script>

<style lang="scss" scoped></style>
