<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>推送管理</el-breadcrumb-item>
        <el-breadcrumb-item>信息推送管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <!-- 标准公告 -->

        <el-tab-pane label="标准公告" name="notice">
          <TableData :dataInfo="dataInfo" :queryForm="queryForm" @getPage="getPage" @getPushPlat="getplatCode" @updateAll="updateAll" />
        </el-tab-pane>

        <!-- WTO/TBT新闻 -->

        <el-tab-pane label="WTO/TBT新闻" name="news">
          <TableData :dataInfo="dataInfo" :queryForm="queryForm" @getPage="getPage" @getPushPlat="getplatCode" @updateAll="updateAll" />
        </el-tab-pane>

        <!-- 法律法规 -->

        <el-tab-pane label="法律法规" name="laws">
          <TableData :dataInfo="dataInfo" :queryForm="queryForm" @getPage="getPage" @getPushPlat="getplatCode" @updateAll="updateAll" />
        </el-tab-pane>

        <!-- 标准水平评价 -->

        <el-tab-pane label="标准水平评价" name="assess">
          <TableData :dataInfo="dataInfo" :queryForm="queryForm" @getPage="getPage" @getPushPlat="getplatCode" @updateAll="updateAll" />
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script>
import TableData from './TableData'
export default {
  components: { TableData },
  data() {
    return {
      activeName: 'notice',

      queryForm: {
        platCode: '',
        size: 10,
        page: 1,
        total: '',
        type:2,
        title:''
      },
      dataInfo: []
    }
  },
  methods: {
    updateAll(e) {
      if(!e)
        return this.$message({ type: 'error', message: '请选择推送平台!' })
      let params = {
        platCode:e,
        type:this.queryForm.type
      }
      this.httpPost('/push/bulletin/updateAll', params).then(ret => {
        this.$message({ type: 'success', message: '更新成功!' })
        this.loadData()
      })
    },
    getPage(e) {
      this.queryForm.page = e
      this.loadData()
    },

    getplatCode(e) {
      this.queryForm.platCode = e
      this.loadData()
    },
    loadData() {
      this.queryForm.total = ''
      this.httpGet('/push/bulletin/query', this.queryForm).then(res => {
        this.dataInfo = res.rows
        this.queryForm.total = res.total
      })
    },
    handleClick(tab, event) {
      this.queryForm.page = 1
      this.queryForm.total = ''
      this.loadData()
      console.log(tab, event)
    }
  },
  watch: {
    activeName(newVal, oldVal) {
    if(newVal=='news'){
        this.queryForm.type=3
      }else if(newVal=='laws'){
        this.queryForm.type=5
      }else if(newVal=='assess'){
      this.queryForm.type=1
      } else{
        this.queryForm.type=2
      }
    }
  },
  mounted() {
  this.loadData()
  }
}
</script>

<style lang="scss" scoped></style>
