<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>推送管理</el-breadcrumb-item>
        <el-breadcrumb-item>标准推送管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>

      <div class="MT_table_cont">
        <!-- 按钮 -->

        <el-form :inline="true" :model="queryForm" class="btn_right demo-form-inline form_border">
          <!-- <div class="tips">默认显示30天内数据</div> -->
          <el-form-item label="" prop="platId">
            <el-select v-model="queryForm.platId" placeholder="请选择推送平台">
              <el-option :label="item.pushName" :value="item.id" v-for="(item, index) in platformList" :key="index"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click.stop="onSubmit">搜索</el-button>
              <span class="btn_cont">
                <span class="choose_btn _color">
                <span style="margin-left: 20px" @click.stop="remove">批量删除</span>
                <span style="margin-left: 20px" @click.stop="allUpdate">批量更新</span>
                <span style="margin-left: 20px" @click.stop="allUpdateByPlatId">全部更新</span>
            </span>
              </span>
          </el-form-item>
        </el-form>
        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
          <el-table-column type="selection" width="55"> </el-table-column>
          <el-table-column prop="code" label="标准编号" width="180"> </el-table-column>
          <el-table-column prop="csName" label="标准名称"> </el-table-column>
          <el-table-column label="标准类型" width="120">
            <template slot-scope="scope">
              {{ scope.row.standType === 1 ? '地方标准' : scope.row.standType === 2 ? '国家标准' : scope.row.standType === 3 ? '国际标准' : '行业标准' }}
            </template>
          </el-table-column>
          <el-table-column label="标准状态" width="120">
            <template slot-scope="scope">
              {{ scope.row.status === 'A' ? '现行' : '作废' }}
            </template>
          </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="100">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row.standardId)">查看</el-link>
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)">删除</el-link>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>
    </el-card>
    <!-- 推送对话框 -->
    <el-dialog title="数据推送" :visible.sync="pushDialogFormVisible" width="30%">
      <el-form>
        <el-form-item label="推送平台" :label-width="formLabelWidth">
          <el-select v-model="selectPlat" placeholder="请选择推送平台">
            <el-option :label="item.pushName" :value="item.id" v-for="(item, index) in platformList" :key="index"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="pushDialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="pushStand">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        tableData: [],
        multipleSelection: [],
        queryForm: {
          size: 10,
          page: 1,
          total: '',
          platId:''
        },
        dialogVisible: false,
        pageType: 0,
        platformList: [],
        pushDialogFormVisible:false,
        formLabelWidth: '120px',
        selectPlat:''
        // ------
      }
    },

    methods: {
      pushStand(){
            this.httpPost('/push/standard/updateByPlat/'+this.selectPlat,'').then(ret => {
              this.$message({ type: 'success', message: '更新成功!' })
              this.pushDialogFormVisible=false
              this.loadData()
            })
      },
      platform() {
        this.httpGet('/push/platform/queryAll', '').then(res => {
          this.platformList = res
        })
      },
      allUpdateByPlatId(){
        this.pushDialogFormVisible = true
      },
      onSubmit() {
        console.log(this.queryForm)
        this.loadData()
      },
      submitForm(formName) {
        this.$refs[formName].validate(valid => {
          if (valid) {
            alert('submit!')
            console.log(this.ruleForm)
          } else {
            console.log('error submit!!')
            return false
          }

          this.dialogVisible = false
        })
      },

      resetForm(formName) {
        this.$refs[formName].resetFields()
        this.dialogVisible = false
      },

      edit(id) {
          this.$router.push({ path: '/standardDetail', name: 'StandardDetail', params: {type:'2', id } })
          this.$sessionData('set', 'stand_id', id)
      },
      set(info) {
        this.$router.push({ path: '/fieldList', name: 'FieldList', params: { info } })
      },

      open(id) {
        this.$message.success('已成功启用')
      },

      close(id) {
        this.$message.success('已成功禁用')
      },

      del(id) {
        this.$confirm('此操作将删除数据, 是否继续?', '提示', { type: 'warning' })
          .then(() => {
            let pushIdList =[]
            pushIdList.push(id)
            this.httpPost('/push/standard/remove', {pushIdList:pushIdList}).then(ret => {
              this.$message({ type: 'success', message: '删除成功!' })
              this.loadData()
            })
          })
          .catch(() => {})
      },

      handleSelectionChange(val) {
        this.multipleSelection = val
      },

      // 全部更新
      allUpdate() {
        this.$confirm('此操作将将更新选中数据, 是否继续?', '提示', { type: 'warning' })
          .then(() => {
            if(this.multipleSelection.length==0)
              return this.$message({ type: 'warning', message: '请先选择要更新的数据!' })
            let pushIdList =[]
            for(var i = 0;i<this.multipleSelection.length;i++){
              pushIdList.push(this.multipleSelection[i].id)
            }
            this.httpPost('/push/standard/updateByStandardId', {pushIdList:pushIdList}).then(ret => {
              this.$message({ type: 'success', message: '更新成功!' })
              this.loadData()
            })
          })
          .catch(() => {})
      },
      // 批量删除
      remove() {
        if (!this.multipleSelection.length) return this.$message.warning('请选择要删除的数据！')
        this.$confirm('此操作将删除已选中数据, 是否继续?', '提示', { type: 'warning' })
          .then(() => {
            let pushIdList =[]
            for(var i = 0;i<this.multipleSelection.length;i++){
              pushIdList.push(this.multipleSelection[i].id)
            }
            this.httpPost('/push/standard/remove', {pushIdList:pushIdList}).then(ret => {
              this.$message({ type: 'success', message: '删除成功!' })
              this.loadData()
            })
          })
          .catch(() => {})
      },

      loadData() {
        this.queryForm.total = ''
        this.httpGet('/push/standard/query', this.queryForm).then(res => {
          this.tableData = res.rows
          this.queryForm.total = res.total
        })
      },
    },

    mounted() {
      this.loadData()
      this.platform()
    }
  }
</script>

<style lang="scss" scoped>
  .item {
    padding: 0 20px 20px;
    display: flex;
    .left {
      flex: 0 0 auto;
      width: 130px;
      text-align: right;
    }
  }
</style>
