<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/newQuery' }">标准查新</el-breadcrumb-item>
        <el-breadcrumb-item>查新检索</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <div class="MT_table_cont item">
        <!-- 按钮 -->
        <div class="btns">
          <el-button @click.stop="reSearch">重新检索</el-button>
          <el-button @click.stop="submit(1)">保存</el-button>
          <el-button type="success" @click.stop="submit(2)">提交</el-button>
          <div class="btn_cont">
            <div class="choose_btn _color">
              <!-- <span @click.stop="exportData">导出数据</span> -->
            </div>
          </div>
        </div>
        <el-table ref="table" border id="detail" :data="tableData" style="width: 100%" @expand-change="expandChange" @row-click="clickRowHandle">
          <el-table-column type="expand">
            <template slot-scope="scope">
              <el-form label-position="left" inline class="demo-table-expand" label-width="140px">
                <el-form-item label="查新项目名称">
                  <span>{{ copyData.proName }}</span>
                </el-form-item>
                <el-form-item label="查新范围">
                  <span>{{ copyData.proRange }}</span>
                </el-form-item>
                <el-form-item label="查新关键字">
                  <el-tag :key="tag" v-for="tag in copyData.keyWords" closable :disable-transitions="false" @close="handleClose(tag)">
                    {{ tag }}
                  </el-tag>
                  <el-input class="input-new-tag" v-if="inputVisible" v-model="inputValue" ref="saveTagInput" size="small" @keyup.enter.native="handleInputConfirm" @blur="handleInputConfirm">
                  </el-input>
                  <el-button v-else class="button-new-tag" size="small" @click="showInput">+ New Tag</el-button>
                </el-form-item>
                <el-form-item label="标准类型(双击修改)" class="stand_type">
                  <el-tabs v-model="activeName" @tab-click="handleClick">
                    <el-tab-pane v-for="(item, idx) in standType" :key="idx" :label="item.name" :name="item.type">
                      <div v-if="copyData[item.type].length">
                        <div class="line_cont stand_item_sort" v-for="(el, index) in copyData[item.type]" :key="index">
                          <div>
                            <div class="edit_item">
                              <span :ref="'editCode_' + index + item.type" v-show="!el.editCode" @dblclick="dblclick(index, 'editCode', item.type)">{{ el.code }}</span>
                              <el-input :name="'editCode_' + index + item.type" v-show="el.editCode" @blur="el.editCode = false" v-model="el.code"></el-input>
                            </div>
                            <div class="edit_item">
                              <span :ref="'editName_' + index + item.type" v-show="!el.editName" @dblclick="dblclick(index, 'editName', item.type)">{{ el.name }}</span>
                              <el-input :name="'editName_' + index + item.type" v-show="el.editName" @blur="el.editName = false" v-model="el.name"></el-input>
                            </div>
                          </div>
                          <div class="move_btn">
                            <i :disabled="index === 0" @click="$toTop(index, copyData[item.type])"><img src="@/assets/images/top.png"/></i>
                            <i :disabled="index === 0" @click="$toPrev(index, copyData[item.type])"><img src="@/assets/images/prev.png"/></i>
                            <i :disabled="index === copyData[item.type].length - 1" @click="$toNext(index, copyData[item.type])"><img src="@/assets/images/next.png"/></i>
                            <i :disabled="index === copyData[item.type].length - 1" @click="$toBottom(index, copyData[item.type])"><img src="@/assets/images/bottom.png"/></i>
                          </div>
                        </div>
                      </div>
                      <div v-else>暂无</div>
                    </el-tab-pane>
                  </el-tabs>
                </el-form-item>
                <el-form-item label="结论">
                  <el-input type="textarea" v-model="copyData.comment" rows="4" style="width: 400px"></el-input>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <el-table-column prop="proName" label="查新项目名称" width="160">
            <template slot-scope="scope">
              <span @click="changeExpandType">{{ scope.row.proName }}</span>
            </template>
          </el-table-column>
          <el-table-column prop="proRange" label="查新范围" width="160"></el-table-column>
          <el-table-column prop="keyWords" label="查新关键字">
            <template slot-scope="scope">
              <el-tag v-for="(item, idx) in scope.row.keyWords" :key="idx">{{ item }} </el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="" label="国行标准">
            <template slot-scope="scope">
              <div class="line_cont" v-for="(item, index) in scope.row.type12.slice(0, 2)" :key="index">
                <div class="line_item">{{ item.code }}</div>
                <div class="line_item">{{ item.name }}</div>
              </div>
              <div class="check_more" v-if="JSON.parse(scope.row.result).type12.length > 2">点击展开更多</div>
            </template>
          </el-table-column>
          <el-table-column prop="keyWords" label="地方标准">
            <template slot-scope="scope">
              <div class="line_cont" v-for="(item, index) in scope.row.type5.slice(0, 2)" :key="index">
                <div class="line_item">{{ item.code }}</div>
                <div class="line_item">{{ item.name }}</div>
              </div>
              <div class="check_more" v-if="JSON.parse(scope.row.result).type5.length > 2">点击展开更多</div>
            </template>
          </el-table-column>
          <el-table-column prop="keyWords" label="团体标准">
            <template slot-scope="scope">
              <div class="line_cont" v-for="(item, index) in scope.row.type3.slice(0, 2)" :key="index">
                <div class="line_item">{{ item.code }}</div>
                <div class="line_item">{{ item.name }}</div>
              </div>
              <div class="check_more" v-if="JSON.parse(scope.row.result).type3.length > 2">点击展开更多</div>
            </template>
          </el-table-column>
          <!-- <el-table-column label="结论" width="300">
            <template slot-scope="scope">
              <el-input type="textarea" v-model="scope.row.comment" rows="2"></el-input>
            </template>
          </el-table-column> -->
        </el-table>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      affirmInfo: {},
      edit: false,
      selectId: '',
      formLabelWidth: '120px',
      exportForm: {
        reportSequence: ''
      },

      standType: [
        { name: '国行标准', type: 'type12' },
        { name: '地方标准', type: 'type5' },
        { name: '团体标准', type: 'type3' }
      ],

      // 要展开的行，数值的元素是row的key值
      expands: [],
      activeName: 'type12',
      copyData: {}, // 当前展开项
      expandType: 1,

      //tag
      inputVisible: false,
      inputValue: '',
      params: {}
    }
  },
  methods: {
    dblclick(index, el, type) {
      //  index, type     // 表格当前项下的数组下标 要改的项de状态 标准类型
      console.log(index, el)
      // this.copyData[idx][this.activeName][index][el] = true
      console.log(this.copyData[this.activeName][index][el])
      this.copyData[this.activeName][index][el] = true

      // 聚焦
      this.$nextTick(() => {
        this.$refs[el + '_' + index + type][0].nextSibling.children[0].focus()
      })
    },
    // tag *****************************************************************

    handleClose(tag) {
      this.copyData.keyWords.splice(this.copyData.keyWords.indexOf(tag), 1)
    },

    showInput() {
      this.inputVisible = true
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    },

    handleInputConfirm() {
      let inputValue = this.inputValue
      if (inputValue) {
        this.copyData.keyWords.push(inputValue)
      }
      this.inputVisible = false
      this.inputValue = ''
    },

    // tag *****************************************************************

    changeType(type) {
      console.log(type)
    },
    clickRowHandle(row, column, event) {
      if (this.expands.includes(row.id)) {
        this.expands = this.expands.filter(val => val !== row.id)
      } else {
        this.expands.push(row.id)
      }
    },

    changeExpandType() {
      this.expandType = 2
    },

    // 切换显示
    handleClick(tab, event) {
      // console.log(this.activeName)
    },
    // 展开行
    clickRowHandle(row) {
      // console.log(row)
      this.$refs.table.toggleRowExpansion(row)
    },
    // 展开关闭
    expandChange(row, expandedRows) {
      // console.log(row, expandedRows)
      this.copyData = row
    },

    // 重新检索
    reSearch() {
      this.loadData(this.params, this.selectId)
    },
    // 提交
    // TODO 获取表格内容得到resultList
    submit(status) {
      let data = this.$deepClone(this.tableData)
      data.map(item => {
        item.keyWords = JSON.stringify(item.keyWords)
        item.result = JSON.stringify({ type12: this.delEditFlag(item.type12), type5: this.delEditFlag(item.type5), type3: this.delEditFlag(item.type3) })
        delete item.type12
        delete item.type5
        delete item.type3
      })

      let params = {
        id: this.selectId,
        resultList: data,
        status: status
      }
      console.log(params)
      this.httpPost('/standard/new/result/submit', params, 20000).then(ret => {
        this.$message({ type: 'success', message: '操作成功!' })
        this.$router.back()
      })
    },
    // 保存
    // save() {
    //   this.tableData.resultNoAndName=this.tableData.resultNameAndNo
    //   let data = JSON.parse(JSON.stringify(this.tableData))
    //   data.map(item => (item.resultNoAndName = this.objArr2str(item.resultNameAndNo)))
    //   console.log(data)
    //   this.httpPost('/standard/ack/result/save/'+this.affirmInfo.id, data).then(ret => {
    //     this.$message({ type: 'success', message: '保存成功!' })
    //     this.loadData('check')
    //   })
    // },

    loadData(params, id) {
      let url = id ? '/standard/new/result/query/' + id : '/standard/new/result/' + Number(params.id)
      let method = id ? 'httpPost' : 'httpGet'
      this[method](url, '').then(res => {
        // console.log(res)
        var data = this.$deepClone(res)
        console.log(res)
        data.map(i => {
          i.keyWords = JSON.parse(i.keyWords)
          i.type12 = JSON.parse(i.result).type12
          i.type5 = JSON.parse(i.result).type5
          i.type3 = JSON.parse(i.result).type3
          i.type12 = this.addEditFlag(i.type12)
          i.type12.type = 'type12'
          i.type5 = this.addEditFlag(i.type5)
          i.type5.type = 'type5'
          i.type3 = this.addEditFlag(i.type3)
          i.type3.type = 'type3'
        })
        this.tableData = data
      })
    },

    addEditFlag(arr) {
      arr.map(item => {
        item.editName = false
        item.editCode = false
      })
      return arr
    },
    delEditFlag(arr) {
      arr.map(item => {
        delete item.editName
        delete item.editCode
        delete item.type
      })
      return arr
    }
  },
  mounted() {
    this.params = this.$sessionData('get', 'checkInfo')
    this.selectId = this.params.id
    if(this.params.status==0)
      this.loadData(this.params,this.selectId)
    else
      this.loadData(this.params)
  }
}
</script>

<style lang="scss" scoped>
.el-row {
  border-bottom: 1px solid #ddd;
  margin-bottom: 20px;
  margin-left: 0 !important;
  margin-right: 0 !important;
}
.grid-content {
  height: 50px;
  line-height: 50px;
}
.item {
  border: none;
  padding: 0;
  // margin-top: -10px;
}
.el-select {
  width: 100%;
}
/deep/.el-table {
  .cell {
    // text-align: center;
  }
}
.el-tag {
  margin: 2px;
}
.check_more {
  color: deepskyblue;
  text-decoration: underline;
  cursor: pointer;
}
.el-form--inline .el-form-item {
  display: block;
  margin-bottom: 0;
}
/deep/.el-form-item__label {
  text-align: right;
}
/deep/.el-form-item__label,
/deep/.el-form-item__content {
  line-height: 36px;
}
.line_cont {
  border-bottom: 1px solid #ddd;
}
.line_item {
  white-space: nowrap;
  width: 100%;
  text-overflow: ellipsis;
  overflow: hidden;
}
.edit_item span {
  cursor: pointer;
}

.edit_item {
  .el-input {
    width: 800px;
  }
}
// tag 888888888888888888888888888888888888888

.button-new-tag {
  margin-left: 10px;
  height: 32px;
  line-height: 30px;
  padding-top: 0;
  padding-bottom: 0;
}
.input-new-tag {
  width: 90px;
  margin-left: 10px;
  vertical-align: bottom;
}

// tag 888888888888888888888888888888888888888

.stand_type {
  /deep/.el-form-item__content {
    width: 1200px;
  }
}
.el-tabs {
  margin-top: -3px;
}
// 排序
.stand_item_sort {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.move_btn {
  line-height: 16px;
  i {
    display: inline-block;
    padding: 2px;
    border: 1px solid #333;
    border-radius: 3px;
    margin: 0 10px;
    cursor: pointer;
  }
}
</style>
