<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>标准查新</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <!-- 标准公告 -->

        <el-tab-pane label="查新中" name="searching">
          <NewQueryTable :dataInfo="dataInfo" :queryForm="queryForm" @getPage="getPage" @getQueryForm="getQueryForm" @reLoad="reLoad" />
        </el-tab-pane>

        <!-- WTO/TBT新闻 -->

        <el-tab-pane label="查新完成" name="done">
          <NewQueryTable :dataInfo="dataInfo" :queryForm="queryForm" @getPage="getPage" @getQueryForm="getQueryForm" @reLoad="reLoad" />
        </el-tab-pane>
      </el-tabs>
    </el-card>
  </div>
</template>

<script>
import NewQueryTable from './NewQueryTable'
export default {
  components: { NewQueryTable },
  data() {
    return {
      activeName: 'searching',

      queryForm: {
        company: '',
        size: 10,
        page: 1,
        total: '',
        status: [0, 1],
        chooseDate: '',
        startDate: '',
        endDate: ''
      },
      dataInfo: []
    }
  },
  methods: {
    reLoad(n) {
      this.loadData()
    },

    getPage(e) {
      this.queryForm.page = e
      this.loadData()
    },

    getQueryForm(e) {
      this.queryForm = e
      this.loadData()
    },
    loadData() {
      this.queryForm.total = ''
      // this.queryForm.status = JSON.stringify(this.queryForm.status).toString()
      this.httpPost('/standard/new/query', this.queryForm).then(res => {
        this.dataInfo = res.rows
        this.queryForm.total = res.total
      })
    },
    handleClick(tab, event) {
      this.queryForm.page = 1
      this.queryForm.total = ''
      this.loadData()
      console.log(tab, event)
    }
  },
  watch: {
    activeName(newVal, oldVal) {
      if (newVal == 'searching') {
        this.queryForm.status = [0, 1]
      } else {
        this.queryForm.status = [2]
      }
    }
  },
  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped></style>
