<template>
  <div>
    <!-- 面包屑导航 -->
    <div class="breadcrumb">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item class="breadcrumb_item" :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>推送管理</el-breadcrumb-item>
        <el-breadcrumb-item>推送配置管理</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card>
      <!-- 功能按钮 -->
      <div class="MT_fn_btn">
        <div class="btn_left"></div>
      </div>

      <div class="MT_table_cont">
        <!-- 按钮 -->
        <div class="btns">
          <el-button @click.stop="add" icon="el-icon-plus">新增</el-button>
          <div class="btn_cont">
            <div class="choose_btn _color">
              <span @click.stop="remove">批量删除</span>
            </div>
          </div>
        </div>
        <!-- 表格列表 -->
        <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
          <el-table-column type="selection" width="55"> </el-table-column>
          <el-table-column prop="pushName" label="平台名称"> </el-table-column>
          <el-table-column prop="pushTable" label="推送表名" width="180"> </el-table-column>
          <el-table-column prop="platCode" label="公告推送编码" width="180"> </el-table-column>
          <el-table-column label="状态" width="120">
            <template slot-scope="scope">
              {{ scope.row.flag === 1 ? '启用' : '停用' }}
            </template>
          </el-table-column>
          <!-- 操作 -->
          <el-table-column label="操作" width="160">
            <template slot-scope="scope">
              <el-link :underline="false" type="primary" @click.stop="edit(scope.row)">修改</el-link>
              <!--<el-link :underline="false" type="primary" @click.stop="set(scope.row)">配置</el-link>-->
              <el-link v-if="scope.row.flag === 1" :underline="false" type="danger" @click.stop="close(scope.row.id)">禁用</el-link>
              <el-link v-else :underline="false" type="primary" @click.stop="open(scope.row.id)">启用</el-link>
              <el-link :underline="false" type="danger" @click.stop="del(scope.row.id)">删除</el-link>
            </template>
          </el-table-column>
        </el-table>

        <!-- 分页器 -->
        <div class="MT_pagination" v-if="queryForm.total">
          <span class="jump_first" @click.stop="$jumpToFirst(queryForm, loadData)">首页</span>
          <span class="jump_last" @click.stop="$jumpToLast(queryForm, loadData)">尾页</span>
          <el-pagination
            background
            :total="queryForm.total"
            :current-page.sync="queryForm.page"
            :page-size.sync="queryForm.size"
            @current-change="loadData"
            layout="total, prev, pager, next, jumper"
          />
        </div>
      </div>

      <!-- 新增/编辑 -->
      <el-dialog :title="pageType === 0 ? '新增配置' : '修改配置'" :visible.sync="dialogVisible" width="30%">
        <el-divider></el-divider>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
          <el-form-item label="平台名称" prop="pushName">
            <el-input v-model="ruleForm.pushName"></el-input>
          </el-form-item>
          <el-form-item label="推送表名" prop="pushTable">
            <el-input v-model="ruleForm.pushTable"></el-input>
          </el-form-item>
          <el-form-item label="公告推送编码" prop="platCode">
            <el-input v-model="ruleForm.platCode"></el-input>
          </el-form-item>
          <el-form-item label="启用标记" prop="flag">
            <el-radio-group v-model="ruleForm.flag">
              <el-radio :label="1">启用</el-radio>
              <el-radio :label="0">禁用</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
            <el-button @click="resetForm('ruleForm')">取消</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      multipleSelection: [],
      queryForm: {
        size: 10,
        page: 1,
        total: ''
      },
      dialogVisible: false,
      pageType: 0,

      // --------------
      ruleForm: {
        pushName: '',
        pushTable: '',
        platCode: '',
        flag: 1,
        dbType:1
      },
      rules: {
        pushName: [{ required: true, message: '请输入平台名称', trigger: 'blur' }],
        pushTable: [{ required: true, message: '请输入推送表名', trigger: 'blur' }],
        platCode: [{ required: true, message: '请输入公告推送编码', trigger: 'blur' }],
        flag: [{ required: true, message: '请选择是否启用', trigger: 'change' }]
      }
    }
  },

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let params = Object.assign({}, this.ruleForm)
          if(this.pageType === 0){
            this.httpPost("/push/platform/add",params).then(res => {
              this.$message.success('增加成功')
              this.dialogVisible=false
              this.loadData()
            })
          }else{
            this.httpPost("/push/platform/mod/"+this.ruleForm.id,params).then(res => {
              this.$message.success('修改成功')
              this.dialogVisible=false
              this.loadData()
            })
          }


        } else {
          console.log('error submit!!')
          return false
        }

        this.dialogVisible = false
      })
    },

    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.dialogVisible = false
    },

    edit(info) {
      this.pageType = 1
      this.ruleForm = info
      this.dialogVisible = true
    },
    // set(info) {
    //   this.$router.push({ path: '/fieldList', name: 'FieldList', params: { info } })
    // },

    open(id) {
      this.$confirm('确定要启用该数据吗?', '提示', { type: 'warning' })
        .then(() => {
          this.httpPost('/push/platform/enable/'+id, "").then(res => {
            this.$message.success('已成功启用')
            this.loadData()
          })
        })
        .catch(() => {})
    },

    close(id) {
      this.$confirm('确定要禁用该数据吗?', '提示', { type: 'warning' })
        .then(() => {
          this.httpPost('/push/platform/disable/'+id, "").then(res => {
            this.$message.success('已成功禁用')
            this.loadData()
          })
        })
        .catch(() => {})
    },

    del(id) {
      this.$confirm('此操作将删除数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          let idList =[]
            idList.push(id)
          this.httpPost('/push/platform/rem', idList).then(ret => {
            this.$message({ type: 'success', message: '删除成功!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },

    // 批量删除
    remove() {
      if (!this.multipleSelection.length) return this.$message.warning('请选择要删除的数据！')
      this.$confirm('此操作将删除已选中数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          let idList =[]
          for(var i = 0;i<this.multipleSelection.length;i++){
            idList.push(this.multipleSelection[i].id)
          }
          this.httpPost('/push/platform/rem', idList).then(ret => {
            this.$message({ type: 'success', message: '删除成功!' })
            this.loadData()
          })
        })
        .catch(() => {})
    },

    add() {
      this.pageType = 0
      this.dialogVisible = true
      console.log(this.pageType)
      this.ruleForm={
          pushName: '',
          pushTable: '',
          platCode: '',
          flag: 1,
          dbType:1
      }
    },

    handleSelectionChange(val) {
      this.multipleSelection = val
    },

    loadData() {
      this.queryForm.total = ''
      this.httpGet('/push/platform/query', this.queryForm).then(res => {
        this.tableData = res.rows
        this.queryForm.total = res.total
      })
    }
  },

  mounted() {
    this.loadData()
  }
}
</script>

<style lang="scss" scoped>
.el-form-item {
  .el-input {
    width: 300px;
  }
}
</style>
