<template>
  <div>
    <!-- 功能按钮 -->
    <div class="MT_fn_btn">
      <el-form :inline="true" :model="queryForm" class="btn_right demo-form-inline form_border">
        <!-- <div class="tips">默认显示30天内数据</div> -->
        <el-form-item label="" prop="pushPlat">
          <el-select v-model="queryForm.platCode" placeholder="请选择推送平台">
            <el-option :label="item.pushName" :value="item.platCode" v-for="(item, index) in platformList" :key="index"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click.stop="onSubmit">搜索</el-button>
        </el-form-item>
      </el-form>
    </div>

    <div class="MT_table_cont">
      <!-- 按钮 -->
      <div class="btns">
        <div class="btn_cont">
          <div class="choose_btn _color">
            <span @click.stop="remove">批量删除</span>
            <span @click.stop="updateAll">全部更新</span>
          </div>
        </div>
      </div>

      <!-- 表格列表 -->
      <el-table ref="multipleTable" :data="tableData" tooltip-effect="dark" style="width: 100%" @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55"> </el-table-column>
        <!--<el-table-column prop="number" label="编号" width="100"> </el-table-column>-->
        <el-table-column prop="title" label="标题"> </el-table-column>
        <el-table-column prop="updateDate" label="更新时间" width="180"> </el-table-column>
        <!-- 操作 -->
        <el-table-column label="操作" width="80">
          <template slot-scope="scope">
            <el-link :underline="false" type="danger" @click.stop="del(scope.row.pushId)">删除</el-link>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页器 -->
      <div class="MT_pagination" v-if="queryForm.total">
        <span class="jump_first" @click.stop="$jumpToFirst(queryForm, changePage)">首页</span>
        <span class="jump_last" @click.stop="$jumpToLast(queryForm, changePage)">尾页</span>
        <el-pagination
          background
          :total="queryForm.total"
          :current-page.sync="queryForm.page"
          :page-size.sync="queryForm.size"
          @current-change="changePage"
          layout="total, prev, pager, next, jumper"
        />
      </div>

      <!-- 推送对话框 -->
      <el-dialog title="数据推送" :visible.sync="pushDialogFormVisible" width="30%">
        <el-form>
          <el-form-item label="推送平台" :label-width="formLabelWidth">
            <el-select v-model="selectPlatCode" placeholder="请选择推送平台">
              <el-option :label="item.pushName" :value="item.id" v-for="(item, index) in platformList" :key="index"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="pushDialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="pushStand">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
export default {
  props: ['dataInfo', 'queryForm'],
  data() {
    return {
      // queryForm:{
      //   platCode:''
      // },
      tableData: [],
      multipleSelection: [],
      platformList: [],
      selectPlatCode:'',
      pushDialogFormVisible:false,
      formLabelWidth: '120px',
    }
  },

  methods: {
    pushStand() {
      if(!this.selectPlatCode)
        return this.$message({ type: 'error', message: '请选择推送平台!' })
      this.pushDialogFormVisible = false
      this.$emit('updateAll', this.selectPlatCode)
    },
    updateAll() {
      this.pushDialogFormVisible = true
    },
    platform() {
      this.httpGet('/push/platform/queryAll','').then(res => {
        this.platformList = res
      })
    },
    del(id) {
      this.$confirm('此操作将删除数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          let pushIdList  =[]
          pushIdList .push(id)
          this.httpPost('/push/bulletin/rem', pushIdList ).then(ret => {
            this.$message({ type: 'success', message: '删除成功!' })
            this.onSubmit()
          })
        })
        .catch(() => {})
    },

    // 批量删除
    remove() {
      if (!this.multipleSelection.length) return this.$message.warning('请选择要删除的数据！')
      this.$confirm('此操作将删除已选中数据, 是否继续?', '提示', { type: 'warning' })
        .then(() => {
          let pushIdList  =[]
          for(var i = 0;i<this.multipleSelection.length;i++){
            pushIdList .push(this.multipleSelection[i].pushId)
          }
          this.httpPost('/push/bulletin/rem', pushIdList ).then(ret => {
            this.$message({ type: 'success', message: '删除成功!' })
            this.onSubmit()
          })
        })
        .catch(() => {})
    },

    handleSelectionChange(val) {
      this.multipleSelection = val
    },

    // 页码改变
    changePage() {
      this.$emit('getPage', this.queryForm.page)
    },
    // 搜索
    onSubmit() {
      this.$emit('getPushPlat', this.queryForm.platCode)
    }
  },

  watch: {
    dataInfo(newVal, oldVal) {
      this.tableData = newVal
    }
  },
  mounted() {
    this.platform()
  }
}
</script>

<style lang="scss" scoped></style>
